html {
	position: relative;
	min-height: 100%;
	height: 100%;
}
body {
	/* Margin bottom by footer height */
	margin-bottom: 60px;
	height: 100%;
}

.page-header {
	border-bottom: 1px solid $color1;
	padding-bottom: 5px;
}

.footer {
	//position:absolute;
	bottom: 0px;
	//width: 100%;
	margin: 0;
	background-color: #000;
	height: 100px; /* or however high you would like */
}

.btn-group {
	margin-bottom: 30px;
	padding: 2px;
}

.btn-group button {
	padding: 1px;
	border-radius: 5px;
	color: $color1;
	background-color: $color2;
}

.btn-group button:hover,
.btn.active {
	padding: 1px;
	border-radius: 5px;
	border-color: $color1;
	color: white;
	background-color: $color1;
	font-weight: bold;
}

.btn.view.active {
	padding: 1px;
	border-radius: 5px;
	border-color: $color1;
	color: white;
	background-color: $color1;
	font-weight: bold;
}

.btn {
	background-color: #e82c75;
}

// Menu items

.menu {
	display: flex;
	align-items: center;
	justify-content: center;
	span {
		max-width: 50%;

		vertical-align: middle;
	}
}



